/* app.css */

.container {
  flex: 1;
  padding: 20px;
}

.title {
  font-size: 24px;
  margin-bottom: 20px;
}

.button {
  margin-bottom: 10px;
}

.user-circle {
  width: 100px;
  height: 100px;
  border-radius: 50%;
  background-color: #007BFF; /* Mavi arka plan */
  display: flex;
  justify-content: center;
  align-items: center;
  margin: 10px;
  cursor: pointer;
  color: white;
  text-align: center;
}

.user-circle:hover {
  background-color: #0056b3; /* Daha koyu mavi hover rengi */
}

.user-list {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-around;
}

.context-menu {
  position: absolute;
  background-color: #ffffff;
  border: 1px solid #ddd;
  box-shadow: 0px 4px 8px rgba(0, 0, 0, 0.2);
  z-index: 1000;
  width: 200px;
  border-radius: 8px;
  padding: 10px;
  display: none; /* Menü başlangıçta gizli olacak */
}

.context-menu-item {
  padding: 8px 12px;
  cursor: pointer;
  font-size: 14px;
  color: #333;
}

.context-menu-item:hover {
  background-color: #f0f0f0;
}
